		&__text
			&-title
				font-size: 36px
			
			&-subtitle
				font-size: 25px

	.desc-title
		font-size: 34px

	.about-page
		&__description
			&-item:not(:last-child)
				margin-bottom: 94px

			&-title
				font-size: 28px

			&-shadow
				bottom: 10px

	.about-chair
		&__content
			font-size: 50px
			padding: 64px 0 68px 0

	.certification 
		&__content
			padding: 76px 0 86px 0

	.discount
		&__arrow
			left: 53%

		&__title
			font-size: 42px

		&__subtitle
			font-size: 18px


@media (max-width: 1366px)
	.delivery
		&__text
			&-title
				font-size: 36px
			
			&-subtitle
				font-size: 24px


@media (max-width: 1200px) 
	.container
		max-width: 920px

	.delivery
		&__inner
			padding-top: 60px
			padding-bottom: 42px

		&__text
			&-title
				font-size: 34px

			&-subtitle
				font-size: 22px
	.call-form
		margin-top: 54px

	.about-page
		&__content
			padding: 68px 0 58px 0

	.about-page
		&__description
			&-item:not(:last-child)
				margin-bottom: 88px

			&-title
				margin-bottom: 24px

				font-size: 26px

			&-image
				width: 280px
				height: 280px

			&-shadow
				bottom: 15px
   	

	.desc-title
		font-size: 32px
	
	.discount
		&__inner
			padding: 54px 0 40px 0

	.about-chair
		&__content
			padding: 62px 0 68px 0

			font-size: 50px

		&__context-number
			font-size: 30px
		
	.certification 
		&__content
			padding: 70px 0 80px 0

		&__about
			margin-top: 74px

		&__desc
			&-title
				margin-left: 18px

	.structure
		&__content
			padding: 120px 0 64px 0

	.free-delivery
		&__inner
			padding: 50px 0 54px 0

		&__circle
			display: none

		&__form
			&-input
				padding: 14px 60px
				margin-bottom: 12px

				text-align: center

			&-btn
				padding: 14px 40px

	.comparison
		&__content
			padding: 56px 0 70px 0


@media (max-width: 1024px)
	.container
		max-width: 860px
		margin: 0 auto

	.header
		&__phone
			align-items: center

			&-number
				font-size: 22px

			&-calling
				padding: 4px 5px

				background-color: $blue
				border: 1px solid $blue
				border-radius: 5px
				text-decoration: none

				color: #fff
				font-size: 18px
				
				&:hover
					color: $blue
					background-color: #fff
		
	.container
		max-width: 900px

	.delivery
		&__text
			&-title
				font-size: 32px

			&-subtitle
				margin-top: 14px

				font-size: 22px

	.free-delivery
		&__arrow
			display: none


@media (max-width: 992px) 
	.container
		max-width: 768px

	.header
		&__content
			max-height: 68px
			margin: 10px 0

		&__phone
			flex-direction: row
			align-items: center
			justify-content: space-between

			&-number
				font-size: 20px
				margin-right: 20px

			&-calling
				margin: 0

	.delivery
		&__inner
			padding-top: 52px
			padding-bottom: 36px
	
	.call-form
		margin-top: 38px

	.about-page
		&__content
			padding: 60px 0 48px 0

		&__title
			margin: 0 auto 116px auto

		&__description
			&-item:not(:last-child)
				margin-bottom: 60px

			&-title
				font-size: 22px

			&-paragraph
				font-size: 16px

			&-image
				width: 260px
				height: 260px

			&-shadow
				margin: 0 auto

				bottom: 10%

			&-round-arrow
				margin-top: -8%

				&--right
					margin-top: -8%

	.desc-title
		font-size: 28px

	.discount
		&__inner
			max-width: 768px
			padding: 48px 0 36px 0

		&__box
			flex-basis: 48%

		&__circle
			display: none

		&__arrow
			display: none

		&__stock-form
			margin-top: 36px

	.about-chair
		&__content
			padding: 54px 0 60px 0

	.certification 
		&__content
			padding: 56px 0 70px 0

	.structure
		&__content
			padding: 60px 0 56px 0

		.top-left, .bottom-left, .top-right, .bottom-right
			display: none
				

	.free-delivery
		&__inner
			max-width: 768px
			padding: 30px 0 32px 0

		&__form
			width: 240px
			margin: 0

			&-block
				max-height: 100px

			&-text
				margin-bottom: 20px

			&-input
				padding: 12px 50px
				margin-bottom: 0

				text-align: center

			&-btn
				padding: 12px 40px
				font-size: 12px


	.comparison
		&__content
			padding: 50px 0 56px 0

		&__row
			display: flex
			justify-content: space-between
			align-items: flex-start

		&__description
			text-align: right
			flex-basis: 30%
			display: flex
			flex-direction: column

			&--blue
				flex-basis: 30%
				
			&-item
				width: 100%

				justify-content: center
				text-align: left

				&--left
					text-align: right

			&-text
				margin: 0
				flex-grow: 1
			
			&-title
				margin-bottom: 28px	

			&-line
				display: none

		&__image
			align-self: center

		&-image
			width: 150px
			height: 250px
			
	.address
		&__content
			padding: 30px 

		&__inner
			max-width: 768px

		&__description
			&-about
				margin-top: 15px
				font-size: 14px

			&-title
				margin-top: 15px
				font-size: 18px

			&-mini-about
				margin-top: 30px	
		
			&-btn
				padding: 14px 36px
				font-size: 14px
	.map
		max-width: 340px

	.geo-icon
		width: 80%



@media (max-width: 860px) 
	.container
		max-width: 720px


@media(max-width: 768px) 
	.container
		max-width: 600px
	 
	.bg
		object-fit: cover

	.header
		&__phone
			&-number
				font-size: 16px

			&-calling
				font-size: 16px
	
	.logo
		max-width: 100px
		max-height: 60px

		&__image
			width: 80%
			height: 80%

		&__name
			font-size: 16px

	.delivery
		&__inner
			padding-top: 30px
			padding-bottom: 20px

		&__about
			flex: 1 0 100%
		
		&__text
			&-title
				font-size: 27px
				line-height: 34px

			&-subtitle
				margin-top: 6px

				font-size: 20px

	.call-form
		margin-top: 30px
		padding: 34px 32px 32px 32px
	
	.btn
		padding: 14px

		font-size: 12px

	.about-page
		&__content
			padding: 38px 0 
		
		&__title
			margin-bottom: 50px

			font-size: 24px

	
		&__description
			&-item
				flex-direction: column
				align-items: center

			&-image
				max-width: 230px
				max-height: 230px
				margin-bottom: 20px

				order: 1

			&-text
				text-align: center
				order: 2

			&-title
				margin-bottom: 10px

				font-size: 20px

			&-paragraph
				font-size: 16px

			&-round-arrow, &-round-arrow--right
				display: none

			&-shadow
				display: none 

	.discount
		&__inner
			padding: 36px 0 26px

		&__title
			font-size: 32px

		&__subtitle
			font-size: 16px
		
		&__stock-form
			margin-top: 18px

	.stock-form
		width: 250px
		height: 190px

		&__order-btn
			margin-top: 26px
			padding: 12px

	.about-chair
		&__content
			padding: 40px 0 46px 0

		&__context
			&-number
				font-size: 26px

	.certification
		&__content
			padding: 40px 0 42px 0

		&__about
			margin-top: 50px

			align-items: flex-start

		&__desc
			&-title
				font-size: 18px

			&-text
				margin-top: 34px

			&-paragraph
				font-size: 16px

				&:not(:last-child)
					margin-bottom: 24px

	.desc-title
		font-size: 24px

	.structure
		&__content
			padding: 40px 0

		&__description
			flex-direction: column 

			&-img
				display: none 

			&-page
				flex-direction: row 

			&-paragraph
				text-align: justify 

	.description-title
		text-align: center 
		font-size: 18px

	.free-delivery
		&__inner
			max-width: 600px			

		&__form
			height: 194px

			&-block
				height: 96px

			&-subtitle
				padding: 0
				background-color: transparent 

	.comparison
		&__content
			padding: 30px 0 44px 0

		&__row
			justify-content: space-around

		&__description
			flex-basis: 45%

			&--blue
				flex-basis: 45%

			&-title
				font-size: 20px
				text-align: center

			&-item
				text-align: center
				&--left
					text-align: center
			
			&-text
				font-size: 14px

		&__image
			display: none 

	.address
		&__content
			padding: 30px 

		&__inner
			max-width: 600px

		&__description
			&-about
				margin-top: 10px
				font-size: 12px

			&-title
				margin-top: 10px
				font-size: 16px

			&-mini-about
				margin-top: 20px	
		
			&-btn
				padding: 10px 26px 
				font-size: 12px
	
	.map
		width: 300px

	.geo-icon
		width: 70%

	.footer
		&__content
			padding: 16px 0

@media (max-width: 640px)
	.container
		max-width: 540px

	.delivery
		&__text
			&-title
				font-size: 24px
				line-height: 30px

			&-subtitle
				font-size: 18px
