			font-size: 26px 
			letter-spacing: 2px 

		&__subtitle
			font-size: 14px 

	.stock-form
		padding: 30px 

		&__date-icon
			max-width: 40px 

		&__order-btn
			width: 180px
			height: 40px

			margin-top: 0

	.about-chair
		&__content
			padding:  34px 0 34px 0 

		&__items
			flex-wrap: wrap
		
		&__item
			flex-basis: 50%
			text-align: center
	
			&:nth-child(1), &:nth-child(2)
				margin-bottom: 30px

			&-desc
				margin-top: 10px

				font-size: 12px

		&__context
			&-number
				font-size: 24px

	.certification
		// display: none
		&__content
			padding: 20px 0



		&__image
			display: none

		&__about
			margin-top: 0

		&__desc
			flex-basis: 100%

			&-title
				// flex-basis: 100%
				// text-align: center
				// font-size: 16px
				display: none

			&-image
				display: none

			&-text
				margin-top: 20px

			&-paragraph
				text-align: center

				&:not(:last-child)
					margin-bottom: 16px

	.desc-title
		font-size: 20px

	.structure
		&__content
			padding: 28px 0 

		&__description
			&-page
				flex-direction: column 
		
			&-block
				justify-content: center 

				&:not(:last-child)
					margin-bottom: 20px

			&-paragraph
				margin-top: 10px
				text-align: center 

	.free-delivery
		&__content
			background-image: linear-gradient(to bottom, #f9fbfd 0%, #ecf3f8 100%)

		&__bg
			display: none

		&__inner
			max-width: 440px
			justify-content: center

		&__form
			max-width: 400px
			flex-grow: 1

			// &-block
			// 	max-height: 84px

			// &-input
			// 	padding: 10px 0

			// &-btn
			// 	font-size: 10px

	.description-title
		font-size: 16px

	.comparison
		&__content
			padding: 24px 0 36px 0

		&__description
			&-title
				font-size: 18px

			&-list
				min-height: 100%

			&-item
				&:nth-child(1), &:nth-child(2)
					display: none
				
				&:last-child
					margin-top: 10px

	.address
		&__content
			padding: 20px

		&__description
			flex-direction: column

			&-image
				margin-bottom: 20px

			&-mini-about
				display: none

			&-btn
				display: none

	.map
		max-width: 100%

	.geo-icon
		display: none
	
	.footer
		&__content
			padding: 10px 0 
	
		&__number
			display: none


