@import '_variable.sass'

*, *:before, *:after
	box-sizing: border-box
	margin: 0
	padding: 0

body
	background-color: #fff
	font-size: 14px
	@extend .regular
	margin: 0 
	padding: 0
	color: $black

 
.container
	max-width: 940px
	margin: 0 auto

button
	border: none
	outline: none

input
	outline: none

.bg
	width: 100%
	height: 100%

	position: absolute
	z-index: -10

.delivery
	&__content
		position: relative

	&__inner
		padding-top: 80px
		padding-bottom: 60px

		display: flex
		justify-content: flex-end

	&__about
		flex-basis: 63.3%
		

		display: flex
		flex-direction: column
		align-items: flex-end
	&__text
		text-align: right
		&-title
			@extend .bold
			font-size: 38px
			line-height: 44px
			letter-spacing: 0.3px
		&-subtitle
			margin-top: 18px

			@extend .medium
			color: $lightBlue
			font-size: 26px
			line-height: 42px
// form block styles
.call-form 
	width: 300px

	margin-top: 70px
	padding: 40px 36px 36px 36px

	border-radius: 20px
	box-shadow: 0 0 38px rgba(104, 176, 201, 0.3)
	background-color: #fff
	// form title
	&__title
		color: $lightBlue
		@extend .medium
		font-size: 20px
		text-align: center
	// form subtitle
	&__subtitle
		margin-top: 16px

		color: #000
		@extend .regular
		font-size: 12px

// form styles
.form
	max-width: 220px
	margin: 19px auto 0 auto
	// input for number
	&__phone-input
		width: 100%

		padding: 15px 68px 13px 68px

		border-radius: 20px
		border: 1px solid #e8e8e8
		
		&::placeholder
			@extend .medium
			font-size: 12px
			color: rgba(156, 156, 156, 0.9)
	
.btn
	width: 100%

	margin-top: 15px
	padding: 17px 43px
	
	border-radius: 23px
	background-color: $blue
	cursor: pointer

	font-family: 'Museo Sans'
	font-weight: 400
	font-size: 14px
	color: #fff
	text-transform: uppercase

	// transition animations
	transition-duration: 0.8s, 1.3s
	transition-property: background-color, color
	transition-delay: 0.2s

	&:hover
		background-color: #449dbb
		color: #ffdb4d

		

// about-page section
.about-page
	&__content
		padding-top: 80px
		padding-bottom: 75px
	// inner-container
	&__inner
		max-width: 780px

		margin: 0 auto

	&__box
		display: flex
		flex-direction: column
		justify-content: space-between 

	&__title
		margin: 0 auto 130px auto

	&__description
		&-items		
			display: flex
			flex-direction: column

		// descriptions block
		&-item
			display: flex
			justify-content: space-between

			position: relative

			&:not(:last-child)
				margin-bottom: 110px

			&--right
				text-align: right

		&-title
			color: $blue
			@extend .bold
			font-size: 30px

			margin-bottom: 32px

		&-paragraph
			color: $black
			font-family: 'Museo Sans', sans-serif
			font-weight: 400
			font-size: 18px

		&-round-arrow
			margin-top: -10%

			position: absolute
			top: 0
			left: 30%

			&--right
				margin-top: -10%

				position: absolute
				top: 0
				left: 38%


		&-images
			max-width: 300px
			height: 330px

			flex-basis: 38.46%

			position: relative
			
		&-image
				width: 300px
				height: 300px

		&-shadow
				position: absolute
				bottom: 0
				left: 20%

.desc-title
	color: $black
	@extend .bold
	font-size: 36px

// discount section styles

.discount
	&__content
		display: flex
		position: relative

	&__inner
		max-width: 940px
		margin: 0 auto
		padding: 70px 0 48px 0

		flex-grow: 1
		display: flex
		justify-content: flex-end

	&__box
		flex-basis: 63.6%

		display: flex
		justify-content: space-between
		align-items: center

	&__circle
		width: 150px
		height: 150px
		padding: 44px 15px 38px 15px

		display: inline-block

		border-radius: 50%
		background-color: #fff

		&-line
			margin: 10px 0
		
		&-inner
			text-align: center

	&__arrow
		margin-left: -10%
		margin-bottom: -50%

	&__column
		display: flex
		flex-direction: column
	
	&__text
		max-width: 350px

		display: flex
		flex-direction: column
		justify-content: space-between
		align-items: center

		color: $black
		text-transform: uppercase

	&__title
		@extend .bold
		font-size: 45px
		display: block

	&__subtitle
		@extend .bold
		font-size: 20px	
	
	&__gift
		display: flex
		align-items: center
		margin: 0 auto
		&-icon
			margin-right: 10px
	
		&-text
			color: $blue
			@extend .bold
			font-size: 20px
			line-height: 40px
			text-transform: lowercase

	&__stock-form
		margin-top: 48px

.circle-line	
	display: inline-block

	width: 120px
	height: 1px

	background-color: #e8e8e8


.stock-form
	width: 275px
	height: 200px

	padding: 37px 34px 35px 34px

	display: flex
	flex-direction: column
	justify-content: space-between
	align-items: center

	border-radius: 23px
	border: 5px solid #fff

	&__date
		width: 207px

		display: flex
		justify-content: space-between

	&__description
		color: #d35656
		@extend .bold
		font-size: 14px
		align-self: center

	&__order-btn
		width: 200px
		height: 50px

		margin-top: 35px
		padding: 15px 61px

		@extend .bold

.old-price
	color: #1e1e1e
	@extend .regular
	font-size: 14px
	text-decoration: line-through

	display: inline-block
.new-price
	color: $red
	@extend .bold
	font-size: 24px


	display: inline-block
		

// about-chair section styles
.about-chair
	&__content
		padding: 70px 0 77px 0

	&__inner
		max-width: 838px
		margin: 0 auto

	&__items
		display: flex
		justify-content: space-between
		align-items: center 

	&__context
		position: relative 

		display: flex
		align-items: center
		justify-content: center

		&-number
			position: absolute 

			color: $blue
			@extend .bold
			font-size: 34px

	&__item-desc
		margin-top: 20px

		color: $black
		@extend .medium
		font-size: 14px

// certification section styles
.certification 
	&__content
		padding: 80px 0 90px 0
		background-image: linear-gradient(to bottom, #f9fbfd 0%, #ecf3f8 100%)

	&__inner
		max-width: 770px
		margin: 0 auto 

	&__column
		display: flex
		flex-direction: column 

	&__title
		align-self: center

		color: $black
		@extend .bold
		font-size: 34px

	&__about
		margin-top: 86px

		display: flex 
		justify-content: space-between 
		align-items: center

	&__desc
		display: flex
		flex-direction: column
		justify-content: space-between 

		&-attention
			display: flex
			align-items: center
 
		&-image
			width: 48px
			height: 40px

			background-color: #f6f9fc
		
		&-title
			margin-left: 24px

			color: $blue
			@extend .bold
			font-size: 22px
			line-height: 1.2

		&-text
			margin-top: 49px
			flex-grow: 1
		
		&-paragraph
			color: #323232
			@extend .medium
			font-size: 18px

		&-paragraph:not(:last-child)
			margin-bottom: 32px

.structure
	&__content
		padding: 132px 0 72px 0
		
	&__description
		display: flex
		justify-content: space-around 

		&-page
			display: flex
			flex-direction: column
			justify-content: space-between
			
			&:first-child
				text-align: right

		&-block
			display: flex

			position: relative

		&-image
			position: absolute
		&-paragraph
			margin-top: 19px

			color: #606060
			@extend .regular
			font-size: 14px

.uppercase-text
	text-transform: uppercase

.bottom-left-block
	margin-bottom: 7%

.top-left
	top: 10%
	right: -58%

.bottom-left
	top: -22%
	right: -57%

.top-right
	top: -30%
	left: -53%

.bottom-right
	left: -55%
	top: 7%


.description-title 
	color: $blue
	@extend .bold
	font-size: 20px

.black-title
		color: $black

.free-delivery
	&__content
		display: flex
		position: relative

	&__inner
		max-width: 940px
		margin: 0 auto

		padding: 58px 0 62px 0

		flex-grow: 1
		display: flex
		justify-content: flex-end

	&__box
		display: flex
		align-items: center

	&__circle
		width: 160px
		height: 158px

		margin-right: 213px

		padding-top: 34px
		padding-bottom: 28px

		background-color: #fff
		border-radius: 50%

		text-align: center

		&-title
			margin-bottom: 20px

			color: $blue
			@extend .bold
			font-size: 16px

	&__form
		margin-right: 14px

		&-text
			margin-bottom: 32px

		&-subtitle
			padding: 6px 2px

			background-color: #fff

		&-block
			height: 120px
		&-input
			padding: 17px 88px
			margin-bottom: 20px

		&-btn
			padding: 20px 60px
			margin: 0

	&__form
		width: 260px
		height: 220px

		display: flex
		flex-direction: column
		justify-content: space-between 

		&-text
			text-align: center

.callback-form
	display: flex
	flex-direction: column
	justify-content: space-between

	&__input
		background-color: #fff
		border: 1px solid #d5e4ef
		border-radius: 20px

		&::placeholder
			color: #9c9c9c
			@extend .medium
			font-size: 13px

	&__btn
		background-color: $blue
		border-radius: 23px
		box-shadow: 0 5px 5px rgba(1, 1, 1, 0.1)
		cursor: pointer

		color: #fff
		text-transform: uppercase

		transition-duration: 0.8s, 1.3s
		transition-property: background-color, color
		transition-delay: 0.2s

		&:hover
			background-color: #449dbb
			color: #ffdb4d

.comparison
	&__content
		padding: 62px 0 78px 0

	&__inner
		max-width: 926px

		margin: 0 auto

	&__row
		display: flex
		justify-content: space-between
		align-items: flex-end

	&__description
		text-align: right

		&-title
			margin-bottom: 46px

			color: $black
			@extend .bold
			font-size: 22px

			&--blue
				color: $blue

		&-list
			max-width: 312px
			width: 100%
			min-height: 288px

			display: flex
			flex-direction: column
			justify-content: space-between
			align-items: center



		&-item
			display: flex
			align-items: center

		&-text
			align-content: flex-start
			color: $black
			flex-basis: 67%
			margin: 0 10px 0 10px

			&--blue
				align-content: flex-start
				color: $blue

	
	&__image
		flex-basis: 21.6%

.description-paragragh 
	font-size: 15px
	@extend .bold	


.address
	&__content
		padding: 50px
		
		background-color: #ecf3f8

	&__inner
		max-width: 844px

		margin: 0 auto

	&__description
		display: flex 
		justify-content: space-between 
		align-items: center 

		&-text
			display: flex 
			flex-direction: column 
			align-items: center 

		&-about
			margin-top: 20px

			color: $black
			@extend .regular
			font-size: 16px

		&-title
			margin-top: 20px

			font-size: 20px
			text-align: center 

		&-mini-about 
			margin-top: 50px

			color: $black
			@extend .regular
			font-size: 16px

		&-btn
			margin-top: 20px
			padding: 16px 44px 16px 45px

			border-radius: 23pc
			box-shadow: 0 5px 5px rgba(1, 1, 1, 0.1)
			background-color: $blue
		
		&-delivery-link
			display: inline-block 

			color: #fff
			@extend .bold
			font-size: 14px 
			text-transform: uppercase
			text-decoration: none

.address-name
	color: $blue
	@extend .bold

.footer
	&__content
		padding: 20px 0 22px 0

	&__row
		display: flex 
		justify-content: space-between 
		align-items: center 

	&__politics
		&-link
			color: $blue
			@extend .regular
			font-size: 14px

	&__number
		color: #505050
		@extend .regular 
		font-size: 14px

.show 
	display: flex

.hide
	display: none

.fade
	animation-name: fade
	animation-duration: 500ms

@keyframes fade 
	from 
		opacity: 0.3

	to
		opacity: 1