@import '_variable.sass'
@import url('https://fonts.googleapis.com/css?family=Source+Sans+Pro&display=swap')
.header
	&__content
		max-height: 72px
		margin: 15px auto
	
	&__column
		display: flex
		justify-content: space-between
		align-items: center

	&__phone
		display: flex
		flex-direction: column
		align-items: flex-end
		&-number
			@extend .bold
			font-size: 24px
		&-calling
			margin-top: 9px

			@extend .medium
			font-size: 14px
			color: $lightBlue
			
			text-decoration: underline
			cursor: pointer

			&:hover
				text-decoration: none

.logo
	max-width: 114px
	max-height: 76px		

	position: relative
	
	&__image
		max-width: 100%

	&__name
		position: absolute
		bottom: 0
		z-index: 1
		color: $blue
		font-family: Source Sans Pro
		font-weight: 600
		font-size: 21px