@import '_variable.sass'

.popup
	.popup__body
		width: 100%
		height: 100%

		position: fixed
		top: 0
		right: 0
		left: 0
		bottom: 0
		margin: 0 auto
		background-color: rgba(0, 0, 0, 0.7)

	&__content
		min-height: 100%
		padding: 32px

		display: flex
		align-items: center
		justify-content: center 

	&__block
		max-width: 300px
		padding: 30px 31px
		background-color: #fff
		box-shadow: 0 0 38ox rgba(103, 175, 201, 0.34)
		border-radius: 20px
		position: relative

	&__form
		width: 100%
		display: flex
		flex-direction: column
		align-items: center

	&__title
		margin-bottom: 14px
		text-align: center

	&__description-text
		margin-bottom: 19px
		text-align: center

	&__input
		max-width: 220px
		padding: 15px 70px 13px 70px 
		margin-bottom: 15px
		border: 1px solid #e8e8e8
		border-radius: 20px
		
		&::placeholder
			@extend .medium
			font-size: 12px
			line-height: 1.2
			color: #9c9c9c

		&:focus
			border: 2px solid #777

	&__btn
		max-width: 226px
		padding: 15px 40px 15px 40px
		background-color: $blue
		box-shadow: 2px 4px 5px rgba(0, 0, 0, 0.1)
		border-radius: 22px
		cursor: pointer
		
		@extend .bold
		font-size: 14px 
		line-height: 1.2
		color: #fff
		text-transform: uppercase

		// animation
		transition-property: background-color, color
		transition-duration: 500ms, 600ms
		transition-timing-function: ease-in-out

		&:hover
			background-color: #34a2eb
			color: #d6eb34


.modal
	&-title
		@extend .bold
		font-size: 19px
		line-height: 1.2
		color: $lightBlue

	&-description-text
		@extend .regular
		font-size: 12px
		line-height: 1.2
		color: #000

.close
	display: inline-block
	width: 24px
	height: 24px
	position: absolute
	top: -44px
	right: 0
	z-index: 999
	cursor: pointer

	&:before, &:after 
		position: absolute
		left: 0
		right: 0
		margin: 0 auto
		content: ''
		display: inline-block
		width: 3px
		height: 24px
		background-color: #5ea5bd
	
	&:before 
		transform: rotate(45deg)

	&:after 
		transform: rotate(-45deg)

