@font-face 
    font-family: 'Museo Sans'
    src: url('../fonts/MuseoSansRegular/MuseoSansCyrl-300.eot')
    src: url('../fonts/MuseoSansRegular/MuseoSansCyrl-300.eot?#iefix') format('embedded-opentype')
    src: url('../fonts/MuseoSansRegular/MuseoSansCyrl-300.woff2') format('woff2')
    src: url('../fonts/MuseoSansRegular/MuseoSansCyrl-300.woff') format('woff')
    src: url('../fonts/MuseoSansRegular/MuseoSansCyrl-300.ttf') format('truetype')
    src: url('../fonts/MuseoSansRegular/MuseoSansCyrl-300.svg#MuseoSansCyrl-300') format('svg')
    font-weight: 300
    font-style: normal

